
import {defineComponent, onMounted, ref} from 'vue'
import EditAvailability from "@/components/Availability/EditAvailability.vue";

export default defineComponent({
    components: {EditAvailability},
    data() {
        return {
            girdData: [{
                    dayOfWeek: 'Monday',
                    startTime: '9:00',
                    endTime: '12:00',
                    recurrence: 'Every week',
                    startDay: '12/02/2021',
                    endDay: '02/05/2021',
                    reason: 'UniversityClasses'
                }, {
                    dayOfWeek: 'Monday',
                    startTime: '9:00',
                    endTime: '12:00',
                    recurrence: 'Every week',
                    startDay: '12/02/2021',
                    endDay: '02/05/2021',
                    reason: 'UniversityClasses'
                }
            ],

        }
    },

    setup () {

        const isEditingDialog = ref(false);
        const EditAvailabilityRef = ref<InstanceType<typeof EditAvailability>>();

        function openEditDialog() {
            isEditingDialog.value = true;
            EditAvailabilityRef.value!.toggleShow();
        }

        onMounted(() => {
            openEditDialog()
        })

        // openEditDialog()
        return { openEditDialog, isEditingDialog, EditAvailabilityRef }
    },

    mounted() {

    },

    methods: {

    }
})
