                    <div class="grid-item">{{data.endTime}}</div>
                    <div class="grid-item">{{data.recurrence}}</div>
                    <div class="grid-item">{{data.startDay}}</div>
                    <div class="grid-item">{{data.endDay}}</div>
                    <div class="grid-item">
                        <button class="green white--text xsmall" @click="openEditDialog">Edit</button>
                        <button class="grey white--text xsmall ml-2">Delete</button>
                    </div>
                    <div class="grid-item">{{data.reason}}</div>
                </template>
            </div>


        </div>

    </div>
<!--    popups  -->
    <edit-availability ref="EditAvailabilityRef" :isEdit="isEditingDialog"></edit-availability>
